import React, { Component } from "react";
import Sketch from "react-p5";
import HicksHexagonDrawer from "./sketch.js"
import './App.css'
import OutlineConfig from "./OutlineConfig.js"
import HexagonConfig from "./HexagonConfig.js"
import Button from '@mui/material/Button';
import Input from '@mui/material/Input';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';



let drawer = null;

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            hexagon: null,
            outlines: [],
            widthDimInput: 0,
            heightDimInput: 0,
            prev_hexagon_props: {
                'dim': [],
                'color': "",
            },
            prev_outline_props: {
                'size': [],
                'color': [],
            },
        }
    }

    has_update = () => {
        if(drawer === null) { return false; }
        return JSON.stringify(this.state.prev_hexagon_props['dim']) !== JSON.stringify(drawer.hexagon_props['dim']) ||
            JSON.stringify(this.state.prev_hexagon_props['color']) !== JSON.stringify(drawer.hexagon_props['color']) ||
            JSON.stringify(this.state.prev_outline_props['size']) !== JSON.stringify(drawer.outline_props['size']) ||
            JSON.stringify(this.state.prev_outline_props['color']) !== JSON.stringify(drawer.outline_props['color']);
    }

    set_prev_props = () => {
        this.setState({
            prev_hexagon_props: JSON.parse(JSON.stringify(drawer.hexagon_props)),
            prev_outline_props: JSON.parse(JSON.stringify(drawer.outline_props))
        })
    }

    setup = (p5, canvasParentRef) => {
        // use parent to render the canvas in this ref
        // (without that p5 will render the canvas outside of your component)
        p5.createCanvas(900, 700).parent(canvasParentRef);
        let hexagon_props = {
            'dim': [26, 14, 8],
            'color': '#981F24'
        }
        let outline_props = {
            'size': [14, 7],
            'color': ['black', '#DF5F18']
        }
        drawer = new HicksHexagonDrawer(p5, p5, hexagon_props, outline_props);
        this.setupHexagon();
        this.setupOutline();
        drawer.draw();
        this.set_prev_props();
    }

    updateOutlineSize = (i, size) => {
        size = size == 0 ? 1 : size;
        drawer.outline_props['size'][i] = size;
        drawer.update_props();
    }

    updateOutlineColor = (i, color) => {
        drawer.outline_props['color'][i] = color;
        drawer.update_props();
    }

    updateHexagonWidth = (width) => {
        width = width == 0 ? 1 : width;
        drawer.hexagon_props['dim'][0] = width;
        drawer.update_props();
    }

    updateHexagonHeight = (height) => {
        height = height == 0 ? 1 : height;
        drawer.hexagon_props['dim'][1] = height;
        drawer.update_props();
    }

    updateHexagonTriangleHeight = (height) => {
        height = height == 0 ? 1 : height;
        drawer.hexagon_props['dim'][2] = height;
        drawer.update_props();
    }

    updateHexagonColor = (color) => {
        drawer.hexagon_props['color'] = color;
        drawer.update_props();
    }


    setupHexagon = () => {
        this.setState({hexagon: <HexagonConfig
            width={drawer.hexagon_props['dim'][0]}
            height={drawer.hexagon_props['dim'][1]}
            triangleHeight={drawer.hexagon_props['dim'][2]}
            color={drawer.hexagon_props['color']}
            colorHandler={this.updateHexagonColor}
            widthHandler={this.updateHexagonWidth}
            heightHandler={this.updateHexagonHeight}
            triangleHeightHandler={this.updateHexagonTriangleHeight}
        />});
    }
    setupOutline = () => {
        let tempOutlines = this.state.outlines.slice();
        if(this.state.outlines.length > drawer.outline_props['color'].length / 2) {
            tempOutlines = this.state.outlines.slice(0, this.state.outlines.length - 1);
        }
        else {
            for(let i = 0; i < drawer.outline_props['color'].length / 2; i++) {
                if(i > this.state.outlines.length - 1) {
                    let currentOutline = <OutlineConfig
                                            id={i}
                                            key={`Outline ${i}`}
                                            title={`Outline ${i}`}
                                            color={drawer.outline_props['color'][i]}
                                            size={drawer.outline_props['size'][i]}
                                            sizeHandler={this.updateOutlineSize}
                                            colorHandler={this.updateOutlineColor}
                                        />
                    tempOutlines.push(currentOutline)
                }
            }
        }
        this.setState({outlines: tempOutlines})
    }


    addOutline = () => {
        drawer.add_outline(5, 'black');
        this.setupOutline();
    }
    remOutline = () => {
        drawer.rem_outline();
        this.setupOutline();
    }

    savePNG = () => {
        let width = parseInt(this.state.widthDimInput);
        let height = parseInt(this.state.heightDimInput);
        if(typeof width !== 'number' || typeof height !== 'number') {
            return;
        }
        let image = drawer.p5.createGraphics(width, height);
        let hexagon_props = {
            'dim': drawer.hexagon_props['dim'],
            'color': '#981F24'
        }
        let outline_props = {
            'size': drawer.outline_props['size'].slice(0, Math.ceil(drawer.n_outline / 2)),
            'color': drawer.outline_props['color'].slice(0, Math.ceil(drawer.n_outline / 2))
        }
        let tempDrawer = new HicksHexagonDrawer(drawer.p5, image, hexagon_props, outline_props)
        tempDrawer.draw();
        image.save("hickshexagon.png");
    }


    draw = (p5) => {
        if(this.has_update()) {
            drawer.draw();
            this.set_prev_props();
        }
    }

    render() {
        let temp = <Input/>
        let saveDimInput = [
            <Input key="widthDimInput" className="saveDimInput" onChange={(event) => {this.setState({widthDimInput: event.target.value})}}/>,
            <Input key="heightDimInput" className="saveDimInput" onChange={(event) => {this.setState({heightDimInput: event.target.value})}}/>,
        ]
        return (
            <div className="App">
            <h1>Hicks' Hexagon</h1>
                <span className="rowContent">
                    <Sketch setup={this.setup} draw={this.draw} />
                    <div style={{display: "flex", flexDirection:"column", justifyContent: "space-between"}}>
                        <div style={{display: "flex", flexDirection: "column", justifyContent: "flex-start"}}>
                            <Card style={{backgroundColor:"#3e4554"}} variant="outlined">
                                <CardContent>

                                <Typography style={{color:"white"}} className="centered" variant="h4">Hexagon</Typography>
                                {this.state.hexagon}

                                <Typography style={{color:"white"}} className="centered" variant="h4">Outlines</Typography>
                                {this.state.outlines}
                                <span style={{display: "flex", justifyContent: "center"}}>
                                    <Button variant="contained" onClick={this.addOutline}>+</Button>
                                    <Button variant="contained" onClick={this.remOutline}>-</Button>
                                </span>

                                </CardContent>
                            </Card>
                        </div>
                        <Card style={{backgroundColor:"#3e4554"}} variant="outlined">
                        <CardContent>
                        <span className="dimInput">
                            {saveDimInput[0]}x{saveDimInput[1]}
                            <Button onClick={() => this.savePNG()}className="saveDimButton" variant="contained">Save png</Button>
                        </span>
                        </CardContent>
                        </Card>
                    </div>
                </span>
            </div>
        );
    }
}

export default App;
